@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,500,500i,600,600i,700,700i&display=swap);
.theme-light &{color:#000;background-color:#fff}.theme-dark &{color:#fff;background-color:#000}html,body{font-family:"Josefin Sans", sans-serif !important}.cursor-pointer{cursor:pointer}.theme-light .guestLanding__auth,.theme-light .landingSlides{background:#fff}.theme-dark .guestLanding__auth,.theme-dark .landingSlides{background:#000}a{text-decoration:none !important}p,a{margin:0 !important}input,textarea,button,select,a{-webkit-tap-highlight-color:rgba(0,0,0,0);outline:none !important;border:none}.theme-light .t-text{color:#000}.theme-dark .t-text{color:#fff}.t-text-lead{color:#db4c3f}.t_heading-smLink{font-size:1.25rem;line-height:2.5rem;font-weight:900;text-transform:capitalize;position:relative}.t_heading-smLink:hover::before{content:"";position:absolute;height:4px;width:50%;bottom:0}.theme-light .t_heading-smLink:hover::before{background:#000}.theme-dark .t_heading-smLink:hover::before{background:#fff}.t_heading-smLink-active:before{content:"";position:absolute;height:4px;width:50%;bottom:0}.theme-light .t_heading-smLink-active:before{background:#000}.theme-dark .t_heading-smLink-active:before{background:#fff}.theme-light .t-desc{color:#ccc}.theme-dark .t-desc{color:#ccc}.t_desc-base{font-size:1rem;line-height:1.25rem}.t_heading-lg{font-size:2.375rem;line-height:2.5rem;font-weight:900;text-transform:capitalize}.t__form-fields-wrap{box-shadow:0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05)}* :focus{outline-color:transparent;outline-style:none}.t__label{display:block;text-align:left;font-size:0.875rem;line-height:1.25rem;color:#ccc;margin:0 !important;text-transform:capitalize;text-transform:capitalize}.t__form-group{overflow:hidden;position:relative;color:#000;padding:0.5rem 0 0.5rem 1.5rem;border:1px solid #ccc}.theme-light .t__form-group{background:#fff}.theme-dark .t__form-group{background:#000}.t__input{width:100%;padding-left:0;background:none;border:none;font-size:0.875rem;line-height:1.25rem;font-weight:bold}.t__input:focus+.t__grow{transform:scaleY(1);opacity:1}.t__grow{height:100%;width:6px;background:linear-gradient(to bottom, #db4c3f, #db4c3f);display:block;transform:scaleY(0);opacity:0;transition:all 0.15s cubic-bezier(0, 0, 0.52, 1.11);position:absolute;left:0;top:0}.t__btn{background:linear-gradient(to bottom, #db4c3f, #db4c3f);color:#fff;display:block;text-transform:capitalize;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06)}.t__btn-lg{width:100%;padding:0.7rem 0}.MuiSlider-thumb,.MuiSlider-track,.PrivateValueLabel-circle-15{background-color:#db4c3f !important}.MuiSlider-rail{background-color:#ddd !important}@media (max-width: 400px){.hideMb{display:none}}

