.item{border-radius:10px;padding:8px;display:flex;align-items:flex-start;box-shadow:rgba(0,0,0,0.2) 0px 2px 1px -1px,rgba(0,0,0,0.14) 0px 1px 1px 0px,rgba(0,0,0,0.12) 0px 1px 3px 0px;justify-content:space-between;min-height:65px;margin-bottom:1rem;width:100%}@media (max-width: 500px){.item{flex-direction:column}}.item__content{flex:0.8 1}@media (max-width: 500px){.item__content{flex:1 1}}.item__content .item-img{object-fit:contain;max-width:100px;border-radius:2px;margin-right:0.5rem}.item__content--createdAt{color:#d1453b;font-size:12px}.item__content--desc{font-size:15px;line-height:21px;min-height:24px}.item__content--head{display:flex;margin-bottom:1rem}.item__actions{flex:0.2 1;display:flex;justify-content:flex-end}@media (min-width: 800px){.item__actions{display:none}}@media (max-width: 500px){.item__actions{flex:1 1;width:100%}}@media (min-width: 800px){.item:hover .item__actions{display:flex}}

